import axios from "axios";
const state = {
  users: [],
};

const getters = {
  allUsers: (state) => state.users,
};

const actions = {
  //Get All the users
  async fetchUsers({ commit }) {
    const response = await axios("http://localhost:6600/api/users/");
    commit("setUsers", response.data);
  },
  async editUser({ commit }, update) {
    console.log(update);
    const response = await axios.put(
      "http://localhost:6600/api/users/",
      update
    );
    console.log(response);
  },
  async changePin({commit},pin){
    const response = await axios.post('http://localhost:6600/api/users/password',pin)
    return response.data
  },
  async deleteUser({ commit }, _id) {
    console.log(_id);
    const response = await axios.delete("http://localhost:6600/api/users/", {
      data: { _id },
    });
    console.log(response);
  },
};
const mutations = {
  setUsers: (state, users) => (state.users = users),
};
export default {
  state,
  getters,
  actions,
  mutations,
};
