import axios from 'axios'

const state = {
    products:[]
}
const getters = {
    allProducts:state => state.products
};

const actions = {
    //Get All the products
    async fetchProducts({commit}){
      const response = await axios('http://localhost:6600/api/products/')
      commit('setProducts',response.data)
    },
    //Upload a file
    async uploadFile({commit},file){
      try {
        const response = await axios.post('http://localhost:6600/api/products/upload',file)
        return response.data.msg
      } catch (error) {
          return error
      }
    }
}

const mutations = {
    setProducts:(state,products)=> state.products = products 
}
export default{
    state,
    getters,
    actions,
    mutations
}