import axios from "axios";

const state = {
  sales: [],
  pendingsales: [],
};
const getters = {
  sales: (state) => state.sales,
  pendingsales: (state) => state.pendingsales,
};

const actions = {
  newSale({ commit }, sale) {
    commit("addSale", sale);
  },
  generateOrderNo({ commit }) {
    const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    const numbers = "0123456789";
    let orderNumber = "";

    // Pick two random chars
    for (var i = 0; i < 2; i++) {
      var rand = Math.floor(Math.random() * alphabet.length);
      orderNumber = orderNumber + alphabet.charAt(rand);
    }
    // Pick four random digits
    for (var i = 0; i < 4; i++) {
      var rand = Math.floor(Math.random() * numbers.length);
      orderNumber = orderNumber + numbers.charAt(rand);
    }

    return "#" + orderNumber;
  },
  editSale({ commit }, details) {
    const editedSale = details.sales.find((sale) => sale.id === details.id);
    editedSale["quantity"] = details.update;
    editedSale["total"] = editedSale.quantity * editedSale.price;
    commit("editSales");
  },
  Editpending( {commit}, sale){
    commit('editPending',sale)
  },
  addtoPending({commit},item){
    commit('changepending',item)
  },
  getPending({commit}){
    commit('pending')
  },
  deleteSale({ commit }, details) {
    const updated = details.sales.filter((sale) => sale.id !== details.id);
    commit("delete", updated);
  },
  addpending({ commit }, pending) {
    commit("addPending", pending);
  },
  removePending({ commit }, orderNumber) {
    commit("remove", orderNumber);
  },
 async totalSales(){
    const totalSales = await axios('http://localhost:6600/api/sales/total')
    const total = totalSales.data.sales.reduce((acc,curr)=>{
         return acc += parseInt(curr.total)
    },0)
    return total;
  },
 async totalExpenses(){
    const totalExpenses = await axios('http://localhost:6600/api/expenses/total')
    const total = totalExpenses.data.expenses.reduce((acc,curr)=>{
       return acc+= curr.amount
    },0)
    return total;
  },
  async addDb({ commit }, sale) {
    try {
      const newSale = await axios.post(
        "http://localhost:6600/api/sales/addsale",
        sale
      );
      if (newSale) {
        commit("reset");
      }
    } catch (error) {
      console.log(error);
    }
  },
  resetSales({ commit }) {
    commit("reset");
  },
 async addExpenseDb({commit},expense){
     const res = await axios.post('http://localhost:6600/api/expenses/addExpense',expense)
    return res.data.msg   
  }
};
const mutations = {
  addSale: (state, sale) => state.sales.push(sale),
  editSales: (state) => state.sales,
  changepending:(state,item) => {
    let prod = state.pendingsales.find(pending=> pending.orderNo === item.orderNo)
    delete item.orderNo
    prod.orders.push(item)
     return state.pendingsales
  },
  editPending: (state,sale) => {
    let orderNo = sale[0]
    sale.shift()
    let prod = state.pendingsales.find(pending=> pending.orderNo === orderNo)
    prod.orders = sale
    return state.pendingsales
  },
  pending:(state)=> state.pendingsales,
  delete: (state, updatedState) => (state.sales = updatedState),
  addPending: (state, pending) => state.pendingsales.push(pending),
  remove: (state, orderNumber) => {
    state.pendingsales = state.pendingsales.filter(
      (pending) => pending.orderNo !== orderNumber
    );
  },
  reset: (state) => (state.sales = []),
};

export default {
  state,
  getters,
  mutations,
  actions,
};
