<template>
  <v-row no-gutters style="height:100vh">
    <v-col>
      <v-container style="height:100%">
    <v-img src="../assets/logo.png" :width="100">
    </v-img>
    <Alert v-if="showAlert" :color="color" :msg="alertMsg"/>    
    <div class="form-section">
       <h1>Welcome Back!</h1>
       <p>Please Log in to your account. Don't have an account?</p>
       <div class="main-form">
         <v-form
    ref="form"
    v-model="valid"
    lazy-validation
    @submit.prevent="validate">
    <v-text-field
      v-model="username"
      label="Username"
      append-icon="fa-solid fa-user-astronaut"
      :rules="[rules.required]"
      required
    ></v-text-field>
<v-text-field
            :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required, rules.min]"
            :type="show3 ? 'text' : 'password'"
            name="input-10-2"
            label="Password"
            hint="At least 4 characters"
            value=""
            v-model="password"
            class="input-group--focused"
            @click:append="show3 = !show3"
          ></v-text-field>
    <div class="btn-section">
    <v-btn
    style="width:100%"
      color="warning"
      class="mr-4"
      type="submit"
    >
      Login
    </v-btn>
    </div>
  </v-form>
       </div>
    </div>
  </v-container>
    </v-col>
    <v-col>
      <v-img
      :aspect-ratio="17/17"
      :width="width"
      src="../assets/login-banner.jpg"
    ></v-img>
    
    </v-col>
  </v-row>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import Alert from '../components/Alert.vue'
export default {
  name:'LoginComponent',
  components:{
    Alert
  },
  computed:{
    ...mapGetters(['isLoggedIn'])
 },
  data(){
    return{
      width:"100%",
      valid: true,
      alertMsg:'',
      showAlert:false,
      checkbox:false,
      color:'',
      username: '',
      show1: false,
        show2: true,
        show3: false,
        show4: false,
        password: '',
        rules: {
          required: value => !!value || 'Required.',
          min: v => v.length >= 4 || 'Min 8 characters',
          }
    }
  },
  methods:{
    ...mapActions(['login']),
    validate(){
      if(this.$refs.form.validate()){
        let user = {
          username:this.username,
          password:this.password,
        }
        this.login(user).then(res=>{
          if(res.status === 200){
            this.$router.push('/dashboard')
          }
        }).catch(err=>{
          this.showAlert = true
          this.color = 'error'
          this.alertMsg = 'Invalid Credentials. Click "forgot password" to reset your password'  
          setTimeout(()=>{
           this.showAlert = false
          },2500)
        })
      }
    }
  }
}
</script>

